.input-error {
  border: 1px solid #fa4c69;
}

.input-warning {
  border: 1px solid #f09700;
}

.input-font-error {
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 130%;
  color: #fa4c69;
  margin-top: 4px;
  margin-bottom: 0;
}

.input-font-warning {
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 130%;
  color: #fcb400;
  margin-top: 4px;
  margin-bottom: 0;
}
